section {
  /*margin-top: 40px;*/
  min-height: calc(100vh - 80px);
}
section .banner {
  margin: 4px auto;
  /**/
  height: calc(100vh - 48px);
  /*margin-top: 6px;*/
  /*transition: 2s;*/
  overflow: hidden;
  -webkit-box-shadow: 0 4px 34px 0 rgba(170,170,170,1);
          box-shadow: 0 4px 34px 0 rgba(170,170,170,1);
  /*background: lightsalmon;*/
}
section .banner:hover {
  -webkit-box-shadow: 0 4px 24px 0 rgba(170,170,170,1);
          box-shadow: 0 4px 24px 0 rgba(170,170,170,1);
}
section .banner img {
  width: 100%;
  height: 100%;
}
section .banner0 {
  background: rgba(33,163,213,.4);
  background-image: -webkit-linear-gradient(#fff 1px, transparent 0),
  -webkit-linear-gradient(left, #fff 1px, transparent 0);
  background-image: -o-linear-gradient(#fff 1px, transparent 0),
  -o-linear-gradient(left, #fff 1px, transparent 0);
  background-image: linear-gradient(#fff 1px, transparent 0),
  linear-gradient(90deg, #fff 1px, transparent 0);
  background-size: 30px 30px;
}
section .banner1 {
  background: rgba(255,255,255,.4);
  background-image: -webkit-gradient(linear, left top, left bottom, color-stop(50%, rgba(200,0,0,.2)), color-stop(0, transparent)),
  -webkit-gradient(linear, left top, right top, color-stop(50%, rgba(200,0,0,.2)), color-stop(0, transparent));
  background-image: -webkit-linear-gradient(rgba(200,0,0,.2) 50%, transparent 0),
  -webkit-linear-gradient(left, rgba(200,0,0,.2) 50%, transparent 0);
  background-image: -o-linear-gradient(rgba(200,0,0,.2) 50%, transparent 0),
  -o-linear-gradient(left, rgba(200,0,0,.2) 50%, transparent 0);
  background-image: linear-gradient(rgba(200,0,0,.2) 50%, transparent 0),
  linear-gradient(90deg, rgba(200,0,0,.2) 50%, transparent 0);
  background-size: 30px 30px;
}
section .banner2 {
  background: rgba(240,240,240,.4);
  background-image: -webkit-radial-gradient(rgba(200,200,200,.4) 30%, transparent 0);
  background-image: -o-radial-gradient(rgba(200,200,200,.4) 30%, transparent 0);
  background-image: radial-gradient(rgba(200,200,200,.4) 30%, transparent 0);
  background-size: 40px 40px;
}
section .banner3 {
  background: -webkit-gradient(linear, left top, left bottom, color-stop(50%, rgba(200,200,200,.2)), to(transparent)),
  -webkit-gradient(linear, left top, right top, color-stop(50%, rgba(200,200,200,.2)), to(transparent));
  background: -webkit-linear-gradient(rgba(200,200,200,.2) 50%, transparent),
  -webkit-linear-gradient(left, rgba(200,200,200,.2) 50%, transparent);
  background: -o-linear-gradient(rgba(200,200,200,.2) 50%, transparent),
  -o-linear-gradient(left, rgba(200,200,200,.2) 50%, transparent);
  background: linear-gradient(rgba(200,200,200,.2) 50%, transparent),
  linear-gradient(90deg, rgba(200,200,200,.2) 50%, transparent);
  /*background-size: 10px 10px;*/
  background: lightsalmon;
}
section ul {
  margin: auto;
  padding: 2vw 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  /*border-top: 1px solid #ddd;*/
}
section ul li {
  margin: 1vw 0 0 1.33vw;
  width: 20vw;
  height: 30vw;
  min-width: 110px;
  min-height: 180px;
  border: 1px solid #eee;
  border-radius: .8vw;
  /*overflow: hidden;*/
  background: #fff;
  -webkit-box-shadow: 4px 4px 24px 0 rgba(238,238,238,1);
          box-shadow: 4px 4px 24px 0 rgba(238,238,238,1);
}
section ul li:hover {
  -webkit-box-shadow: 2px 4px 8px 0 rgba(238,238,238,.8);
          box-shadow: 2px 4px 8px 0 rgba(238,238,238,.8);
}
section ul li:nth-of-type(4n+1) {
  margin-left: 0;
}
section ul li dl dt {
  position: relative;
  padding: .4vw;
  height: 20vw;
  overflow: hidden;
  /*transition: 1s;*/
  /*opacity: .8;*/
  background: #fff;
}
section ul li dl dt img.picGoods-min {
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
  width: 100%;
  height: 100%;
  /*border-radius: .2vw .2vw 0 0;*/
  /*box-shadow: 0 0 4px 2px #ccc;*/
}
section ul li dl dt img.picGoods-min:hover {
  width: 110%;
  height: 110%;
  /*transform: translateZ(100px);*/
  -webkit-transform: translateX(-5%) translateY(-5%);
      -ms-transform: translateX(-5%) translateY(-5%);
          transform: translateX(-5%) translateY(-5%);
}
section ul li dl dd {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
  padding: 0.4vw 0 1vw;
  height: 10vw;
}
section ul li dl dd span {
  font-size: 0.9vw;
  color: #aaa;
}
section ul li dl dd b {
  color: #f00;
}
section ul li dl dd a.get-btn {
  cursor: pointer;
  padding: 0.4vw 4vw;
}

.register,
.login {
  z-index: 1;
  position: fixed;
  top: 50%;
  left: 50%;
  display: none;
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
  -webkit-transform: translateX(-50%) translateY(-50%);
      -ms-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
  padding: 50px 30px;
  width: 400px;
  height: 300px;
  -webkit-box-shadow: 0 2px 2px 1px #ccc;
          box-shadow: 0 2px 2px 1px #ccc;
  background: rgba(255,255,255,.6);
}
.register i,
.register b {
  cursor: pointer;
  position: absolute;
}
.register i {
  top: 0;
  right: 10px;
  -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
  font-size: 30px;
  color: #ccc;
}
.register b {
  right: 30px;
  bottom: 36px;
  padding: 6px 14px;
  border-radius: 2px;
  -webkit-box-shadow: 1px 1px 1px #ccc;
          box-shadow: 1px 1px 1px #ccc;
  font-size: 14px;
  color: rgba(33,163,213,.6);
  /*color: rgba(145,181,77,1);*/
}
.register input {
  width: 100%;
  height: 36px;
  /*border-radius: 8px/4px;*/
  /*box-shadow: inset 0 2px 2px 2px #eee;*/
  background: transparent;
  text-indent: 10px;
  color: #999;
}
.register input:nth-of-type(2) {
  margin: 26px 0 30px;
}
.register a {
  cursor: pointer;
  display: block;
  padding: 0;
  line-height: 40px;
  /*box-shadow: 1px 1px 1px rgba(145,181,77,.8);*/
  /*background: rgba(145,181,77,1);*/
  background: rgba(33,163,213,.6);
}

#back-top {
  z-index: 2;
  position: fixed;
  right: 4vw;
  bottom: 40px;
  display: none;
  width: 0;
  height: 0;
  border-width: 0 1.6vw 1.6vw 1.6vw;
  border-style: solid;
  border-color: transparent transparent rgba(33,163,213,.6) transparent;
}
#timeLine {
  z-index: 2;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: .2vw;
}


