#detail {
  margin: 20px auto;
  /*margin-top: calc(40px + 1vw);*/
  padding: 1vw;
  -webkit-box-shadow: 1px 1px 2px 1px #eee;
          box-shadow: 1px 1px 2px 1px #eee;
  background: rgba(255,255,255,1);
}
dl.goods-property {
  display: -ms-flexbox;
  display: flex;
  padding: 1vw;
  -webkit-box-shadow: 1px 1px 1px 1px #eee;
          box-shadow: 1px 1px 1px 1px #eee;
}
dl.goods-property dt img {
  padding: .6vw;
  width: 40vw;
  height: 40vw;
  -webkit-transition: .6s;
  -o-transition: .6s;
  transition: .6s;
  -webkit-box-shadow: 4px 4px 24px 0 rgba(238,238,238,1);
          box-shadow: 4px 4px 24px 0 rgba(238,238,238,1);
  /*box-shadow: 0 0 4px 2px #eee;*/
}
dl.goods-property dt img:hover {
  /*padding: 0;*/
  /*width: 41vw;*/
  /*height: 41vw;*/
  -webkit-box-shadow: 2px 4px 8px 0 rgba(238,238,238,.8);
          box-shadow: 2px 4px 8px 0 rgba(238,238,238,.8);
  /*box-shadow: 0 2px 10px 2px #eee;*/
}
dl.goods-property dd {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
  width: 100%;
  margin-left: 4vw;
  padding: 1.4vw 0 3vw;
}
dl.goods-property dd span:before,
dl.goods-property dd b.numSale:before,
dl.goods-property dd div.goods-property-num:before {
  position: absolute;
  left: -4vw;
  color: #aaa;
  font-size: .9vw;
}
dl.goods-property dd span:before {
  content: '价格';
}
dl.goods-property dd b.numSale:before {
  content: '销量';
}
dl.goods-property dd div.goods-property-num:before {
  content: '数量';
}
dl.goods-property dd span,
dl.goods-property dd b.numSale,
dl.goods-property dd div.goods-property-num{
  position: relative;
  text-indent: 6vw;
}
dl.goods-property dd span {
  margin: -5vw 0;
  font-size: 1.4vw;
  color: #f00;
}
dl.goods-property dd b.numSale {
  color: #f00;
}
dl.goods-property dd div.goods-property-num i {
  cursor: default;
  padding: 0 .4vw;
  -webkit-box-shadow: 0 0 1px 1px #ddd;
          box-shadow: 0 0 1px 1px #ddd;
}
dl.goods-property dd div.goods-property-num i:first-of-type:hover {
  -webkit-box-shadow: 0 1px 1px 2px #ddd;
          box-shadow: 0 1px 1px 2px #ddd;
}
dl.goods-property dd div.goods-property-num i:last-of-type:hover {
  -webkit-box-shadow: 0 -1px 1px 2px #ddd;
          box-shadow: 0 -1px 1px 2px #ddd;
}
dl.goods-property dd div.goods-property-num input {
  margin: 0 1vw;
  padding: 0 .2vw;
  width: 2vw;
  /*border: 0;*/
  text-align: center;
}
dl.goods-property dd a.get-btn {
  cursor: pointer;
  width: 20vw;
  margin-left: 6vw;
  padding: .6vw 0;
}

div.goods-detail {
  margin-top: 2vw;
  /*padding-left: 1vw;*/
  -webkit-box-shadow: 0 1px 1px 1px #eee;
          box-shadow: 0 1px 1px 1px #eee;
}
div.goods-detail div.goods-detail-link {
  border-bottom: 2px solid #ddd;
}
div.goods-detail div.goods-detail-link a {
  display: inline-block;
  padding: 1vw 1vw;
  font-size: 1.2vw;
  border: 1px solid transparent;
  border-bottom: 0;
  border-top: 4px solid transparent;
}
div.goods-detail div.goods-detail-link a.active {
  border: 1px solid rgba(33,163,213,.8);
  border-bottom: 0;
  border-top: 4px solid #f00;
  color: #f00;
  font-weight: 600;
}
div.goods-detail div.goods-detail-info-list span {
  display: inline-block;
  width: 33.3%;
}
div.goods-detail ul.goods-detail-img li img {
  width: 100%;
}

#comment ul.comment-list {
  padding: 1vw;
}
#comment ul.comment-list li {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  padding: 1vw 0;
  border-top: 1px solid #ccc;
}
#comment ul.comment-list li:first-of-type {
  border: 0;
}
#comment ul.comment-list li div:nth-of-type(1) {
  -ms-flex: 6 1;
      flex: 6 1;
}
#comment ul.comment-list li div:nth-of-type(2) {
  -ms-flex: 2 1;
      flex: 2 1;
  text-align: right;
}

#cart-add-icon {
  right: 10.2vw;
  bottom: 5.8vw;
}


